@font-face{font-family:'Wes FY';src:url("./promo-js/fonts/wes-fy/Black-Foundry-Wes-FY-Regular.ttf");font-display:swap;font-weight:normal}@font-face{font-family:'Wes FY';src:url("./promo-js/fonts/wes-fy/Black-Foundry-Wes-FY-Black.ttf");font-display:swap;font-weight:900}@font-face{font-family:'Wes FY';src:url("./promo-js/fonts/wes-fy/Black-Foundry-Wes-FY-Medium.ttf");font-display:swap;font-weight:500}@font-face{font-family:'Wes FY';src:url("./promo-js/fonts/wes-fy/Black-Foundry-Wes-FY-Bold.ttf");font-weight:bold;font-display:swap}.header{background:#FFFFFF;height:72px;border-bottom:1px solid #EAEBEF;display:flex;align-items:center;padding:0 24px;width:calc(100% - 48px);justify-content:space-between;min-height:72px;position:relative;z-index:900}.header__logo{margin-right:32px}.header__separator{height:34px;width:1px;background-color:#DFE1E6}.header__content{flex:1;justify-content:space-between;display:none;align-items:center}.header__section{display:flex;align-items:center}.header__section>*{margin-left:32px}.header__section>*:first-child{margin-left:0}.header__section .app-button+.app-button{margin-left:24px}@media only screen and (min-width: 468px){.header{padding:0 32px;width:calc(100% - 64px)}.header__content{display:flex}}.header .nav-button:hover{color:#3357FF}.header .app-button{height:32px;padding:0 32px;font-size:14px}
