@font-face{font-family:'Wes FY';src:url("./promo-js/fonts/wes-fy/Black-Foundry-Wes-FY-Regular.ttf");font-display:swap;font-weight:normal}@font-face{font-family:'Wes FY';src:url("./promo-js/fonts/wes-fy/Black-Foundry-Wes-FY-Black.ttf");font-display:swap;font-weight:900}@font-face{font-family:'Wes FY';src:url("./promo-js/fonts/wes-fy/Black-Foundry-Wes-FY-Medium.ttf");font-display:swap;font-weight:500}@font-face{font-family:'Wes FY';src:url("./promo-js/fonts/wes-fy/Black-Foundry-Wes-FY-Bold.ttf");font-weight:bold;font-display:swap}.app-customer-settings-page{display:flex;margin:16px 0}.app-customer-settings-page .step-details{min-height:calc(100vh - 64px);width:38%;justify-content:flex-start;position:relative;background-image:url("./images/settings-collage.png");background-position:bottom;background-repeat:no-repeat;background-size:100%}.app-customer-settings-page .step-details__content{height:100%;display:flex;flex-grow:1;padding-top:40%}@media screen and (min-height: 1000px){.app-customer-settings-page .step-details__content{padding-top:50%}}.app-customer-settings-page .step-details__title{text-align:center;display:flex}@media screen and (min-width: 1280px) and (max-width: 1600px){.app-customer-settings-page .step-details__title{font-size:40px;line-height:52px}}@media screen and (min-width: 1024px) and (max-width: 1280px){.app-customer-settings-page .step-details__title{font-size:32px;line-height:40px}}.app-customer-settings-page__form{width:316px;margin:0 auto;display:flex;flex-direction:column;justify-content:center;align-content:center}.app-customer-settings-page__title{font-size:20px;font-weight:bold;height:24px;color:#000;letter-spacing:0;white-space:nowrap;margin:0}.app-customer-settings-page__error{font-weight:500;color:red;font-size:14px;line-height:17px;margin-top:8px}.app-customer-settings-page__input{margin-top:24px}.app-customer-settings-page__input .form-input--invalid{margin-bottom:10px}.app-customer-settings-page__label{display:block;font-size:14px;height:20px;letter-spacing:0;margin:0 0 6px}.app-customer-settings-page__submit{border-radius:500px;margin-top:24px}.app-customer-settings-page__submit.app-button{height:40px}.app-customer-settings-page__reset{color:#9396a0;transition:all 200ms ease;font-size:14px;letter-spacing:0;margin-top:24px;text-align:center;cursor:pointer}.app-customer-settings-page__reset:hover{color:#4b4e58}
